import Vue from 'vue'
import Vuex from 'vuex'

import Demo from './modules/demo'
import Login from './modules/login'
Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    Demo,
    Login
  },
  strict: process.env.NODE_ENV !== 'production'
})



