<template>
  <div id="app">
    <Login v-if="LoginShow"></Login>
    <router-view/>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Login from '@/components/login'
export default {
  name: 'App',
  components: {
    Login
  },
  computed: {
    ...mapState({
      LoginShow: state => state.Login.LoginState
    })
  }
}
</script>

<style lang='less'>
@import 'assets/css/reset.less';
#app{
  width: 100%;
  height: 100%;
}
</style>



